import { Component, Input } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { DialogService } from 'src/app/core/dialog/dialog.service';
import { ModalOptions } from 'src/app/core/dialog/modal/modal-options';

@Component({
  selector: 'pulse-pic',
  templateUrl: './pulse-pic.component.html',
  styleUrls: ['./pulse-pic.component.scss']
})
export class PulsePicComponent {
  @Input()
  public src: string;

  @Input()
  public title: string;

  @Input()
  public zoom: boolean = true;

  @Input()
  public size: 'sm' | 'md' | 'md-dyn' | 'lg' = 'lg';

  public showText: boolean;

  constructor() { }


  public hideTitle() {
    this.showText = false;
  }
  public showTitle() {
    this.showText = true;
  }
}